@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";

.search-page {
    background-color: #fff;

    * {
        // font-family: 'Myriad Pro' !important;
        font-weight: 400;
        font-style: normal;
        color: #353535;
        text-decoration: none;

        min-height: 0;
        min-width: 0;
    }
    .container {
        width: 100%;
        max-width: 1280px;
        margin: 0 auto;
        padding: 0 55px; 
        box-sizing: border-box;      
    }

    .search-page__head {
        min-height: 50vh;
        background-image: linear-gradient(90deg, rgba(0, 116, 255, 0.2) 0%, rgba(103, 30, 166, 0.2) 100%), linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 100%), url('../img/bg-line.svg');        
        background-color:  #6A22A8;
        background-position: center;
        background-repeat: no-repeat;
        .container {
            min-height: inherit;
        }
        .search-page__head__breadcrumps-wrap {
            padding-top: 20px;
        }
        .search-page__head__wrap {
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            min-height: inherit;
        }
        .search-page__head__header {
            font-family: 'Avenir Next Cyr';
            font-style: normal;
            font-weight: 750;
            font-size: 48px;
            line-height: 59px;
            /* identical to box height */
            color: #FFFFFF;
            margin-bottom: 22px;
        }
        .search-page__head__desc {
            font-family: 'Avenir Next Cyr';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 17px;
            text-align: center;
            
            color: #FFFFFF;
            margin-bottom: 40px;
        }
        .search-page__head__form {
            max-width: 770px;
            width: 100%;
            position: relative;
            box-sizing: border-box;
        }
        .search-page__head__search {
            max-width: inherit;
            width: inherit;
            padding: 12px 50px 12px 20px;
            box-sizing: border-box;
            background: #FFFFFF;
            /* Violet */
            border: 1px solid #36337A;

            border-radius: 8px;
            outline: none;  
            
            font-family: 'Avenir Next Cyr';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 17px;
            
            color: rgba(36, 36, 36, 0.8); 
            position: relative;
            z-index: 100;           
        }
        .search-page__head__form__submit {
            background: none;
            border: none;
            outline: none;
            position: absolute;
            top: 3px;
            right: 0;
            height: 100%;
            width: 48px;
            cursor: pointer;
            z-index: 100;
        }
        .search-page__head__form__list {
            position: absolute;
            top: 90%;
            width: 100%;
            background: #fff;
            padding-top: 3px;
            border-radius: 0px 0px 5px 5px;
        }
        .search-page__head__form__list__item {
            padding: 15px 20px;
            &:not(:last-child) {
                border-bottom: 1px solid rgba(36, 36, 36, 0.2);
            }

            &:hover {
                background: #36337A;
                a {
                    color: #fff;
                }
            }
        }
    }

    .search-page__body {
        .container {

        }
        .search-page__body__list {
            padding-top: 64px;
        }
        .search-page__body__list__item {
            display: grid;
            grid-template-columns: 30px auto;
            column-gap: 20px;
            // &:not(:last-child) {
                border-bottom: 1px solid rgba(36, 36, 36, 0.2);
                padding-bottom: 45px;
                margin-bottom: 45px;
            // }

            &:last-child {
                margin-bottom: 60px;
            }
        }
        .search-page__body__list__item__num {
            font-family: 'Avenir Next Cyr';
            font-style: normal;
            font-weight: 750;
            font-size: 18px;
            line-height: 22px;
            /* identical to box height */
            /* Violet */
            color: #36337A;    
            width: 100%;
            height: 30px;
            border: 1px solid #36337A;
            border-radius: 50%;

            display: flex;
            justify-content: center;
            align-items: center;
        }
        .search-page__body__list__item__wrap {

        }
        .search-page__body__list__item__wrap__section {
            font-family: 'Avenir Next Cyr';
            font-style: normal;
            font-weight: 700;
            font-size: 14px;
            line-height: 17px;
            display: flex;
            align-items: flex-end;
            
            color: #989898;
            display: block;  
            margin-bottom: 5px;          
        }
        .search-page__body__list__item__wrap__name {
            display: block;
            font-family: 'Avenir Next Cyr';
            font-style: normal;
            font-weight: 750;
            font-size: 36px;
            line-height: 44px;
            /* Violet */
            color: #36337A; 
            margin-bottom: 10px;           
        }
        .search-page__body__list__item__wrap__desc {
            font-family: 'Avenir Next Cyr';
            font-style: normal;
            font-weight: 450;
            font-size: 16px;
            line-height: 140%;
            /* or 22px */
            color: #353535;
        }
        .search-page__body__list__item__wrap__link {
            display: block;
            border: 1px solid #36337A;
            border-radius: 8px;
            padding: 12px 0;
            max-width: 179px;
            width: 100%;
            text-align: center;

            font-family: 'Avenir Next Cyr';
            font-style: normal;
            font-weight: 450;
            font-size: 14px;
            line-height: 17px;
            /* Violet */
            color: #36337A; 
            
            margin-top: 10px;
        }

        .search-page__body__list__item__wrap__list {
            margin-top: 17px;
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(250px, 257px));
            gap: 50px 30px;
        }
        .search-page__body__list__item__wrap__list__item {
            max-width: 270px;
            width: 100%;
        }
        .search-page__body__list__item__wrap__list__item__link {
            display: block;
            position: relative;
            max-width: inherit;
            width: 100%;

            &:hover .search-page__body__list__item__wrap__list__item__overlay{
                opacity: 1;
            }
            .search-page__body__list__item__wrap__list__item__overlay {
                position: absolute;
                top: 0;
                left: 0;
                background: rgba(0, 0, 0, 0.5);
                height: 384px;
                width: 100%;
                opacity: 0;
                transition: opacity .2s ;
            }
            img {
                max-width: inherit;
                width: inherit;
                max-height: 384px;
                height: 100%;
                object-fit: cover;
                margin-bottom: 20px;
            }
            .search-page__body__list__item__wrap__list__item__header {
                display: block;
                margin-bottom: 4px;

                font-family: 'Avenir Next Cyr';
                font-style: normal;
                font-weight: 700;
                font-size: 18px;
                line-height: 22px;
                /* identical to box height */
                /* Violet */
                color: #36337A;                
            }
            .search-page__body__list__item__wrap__list__item__desc {
                display: block;
                font-family: 'Avenir Next Cyr';
                font-style: italic;
                font-weight: 400;
                font-size: 16px;
                line-height: 20px;
                /* identical to box height */
                color: #353535;                
            }
        }
        .show-more__link {
            display: block;
            max-width: 198px;
            width: 100%;
            padding: 12px 0;
            box-sizing: border-box;
            margin: 0 auto;
            background: #36337A;
            border-radius: 8px;

            font-family: 'Avenir Next Cyr';
            font-style: normal;
            font-weight: 450;
            font-size: 14px;
            line-height: 17px;
            text-align: center;
            
            color: #FFFFFF;            
        }
    }

    .empty {
        padding: 60px 0 120px;
        .container {

        }
        .empty__desc {
            font-family: 'Avenir Next Cyr';
            font-style: normal;
            font-weight: 750;
            font-size: 36px;
            line-height: 44px;
            
            /* Violet 2 */
            color: #671EA6;
            display: block;
            margin-bottom: 40px;
        }
        .empty__wrap {

        }
        .empty__wrap__header {
            font-family: 'Avenir Next Cyr';
            font-style: normal;
            font-weight: 700;
            font-size: 24px;
            line-height: 29px;
            /* Violet 2 */
            color: #671EA6;
            display: block;
            margin-bottom: 30px;
        }
        .empty__wrap__list {
            display: flex;
            justify-content: start;
            align-items: center;
            flex-wrap: wrap;
        }
        .empty__wrap__list__item {
            cursor: pointer;
            border: 1px solid #242424;
            border-radius: 50px;
            padding: 12px 20px;

            display: flex;
            justify-content: start;
            align-items: center;
            img {
                margin-left: 6px;
            }       
            span {
                font-family: 'Avenir Next Cyr';
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 17px;
                color: rgba(36, 36, 36, 0.8); 
            }

            &:not(:last-child) {
                margin-right: 20px;
            }
        }
    }

}

@media screen and (max-width: 530px) {
    .search-page .search-page__body .search-page__body__list__item__wrap__name {
        font-size: 24px;
        line-height: 100%;
    }
    .search-page .search-page__head .search-page__head__header {
        text-align: center;
        font-size: 36px;
        margin-bottom: 12px;
        line-height: 100%;
    }
    .search-page .search-page__head .search-page__head__desc {
        margin-bottom: 20px;
    }
}